// surface_cleaner, hand SideNavItem, wipes


class Call_modal extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showWarning: true };
    this.handleToggleClick = this.handleToggleClick.bind(this);
  }

  handleToggleClick() {
    this.setState(state => ({
      showWarning: !state.showWarning
    }));
  }
  componentDidMount() {
    // Auto initialize all the things!
    M.AutoInit();
  }

  state = {
    isActive: false
  }

  handleShow = (indexToShow) => {


    this.setState({
      isActive: true,
      indexToShow: indexToShow
    })
  }
  handleHide = () => {
    this.setState({
      isActive: false,
      indexToShow: void 0
    })
  }
  render() {
    return (
      <div>
        {this.state.isActive ?
          <Row >
            {
              Listings.map(
                (id, index) =>
                  index !== this.state.indexToShow ? null :
                    <span>
                      {id.products}
                      <div onClick={this.handleHide} className="secondary fixed-action-btn direction-top active">
                        <a className="btn-floating btn-large red">
                          <i className="large material-icons">close</i>
                        </a>
                      </div>
                    </span>
              )
            }
          </Row>
          :
          <Row >
            <h2>Product Catagories</h2>
            <div className='card'>
            {
              Listings.map(
                (id, index) =>
                  <Col s={4} m={3}>
                    <img
                      onClick={() => {
                        this.handleShow(index)
                      }}
                      className='materialboxed responsive-img'
                      src={id.img}
                    />
                  </Col>
              )
            }
            </div>
          </Row>
        }

      </div>

    );
  }
}





export default Call_modal;